.kromac-avatar-content {
  position: relative;
  height: auto;
}

.kromac-avatar-container {
  position: relative;
  margin-bottom: 1em;

  &:hover > .kromac-tooltip {
    background: #000;
    color: #fff;
    padding: .5em;
    transform: scale(1);
    top: -20px;

    &::before {
      content: '';
      width: 10px;
      height: 10px;
      border-left: solid 5px transparent;
      border-right: solid 5px transparent;
      border-top: solid 5px #000;
      position: absolute;
      bottom: -1.25em;
      left: 15%;
    }
  }

  .kromac-tooltip {
    z-index: 100;
    background: #000;
    color: #fff;
    padding: .5em;
    font-size: .5em;
    width: max-content;
    position: absolute;
    transform: scale(0);
    top: 0;
    left: calc(var(--spanLeft)/2 - 15px);
    transition: top 2s ease, transform 2s ease;
  }

  .kromac-avatar {
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    height: auto;
    width: max-content;

    .kromac-avatar-section {
      position: relative;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;

      .kromac-skeleton {
        z-index: 1;
      }

      img {
        position: absolute;
        width: auto;
        height: 100%;
      }
    }

    &:hover > .kromac-avatar-name {
      transform: var(--transformHover);
      transition: 2s transform ease;
    }

    .kromac-avatar-name {
      display: flex;
      align-items: center;
      transform: var(--transform);
      padding-left: calc(var(--paddingLeft) + 1em);
      z-index: -1;
      padding-right: 2em;
      color: #fff;
      border-radius: 0 75px 75px 0;
      transition: 2s transform ease;

      h3 {
        margin-bottom: 0;
      }
    }
  }
}
